
#app {
  width: 80%;
  max-width: 1176px;
  margin: auto;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.gallery__header {
  padding-bottom: 72px;
  text-align: left;
}
.gallery__header > h1 {
  font-family: 'Courgette';
  font-size: 36px;
  font-weight: 400;
  color: #111;
}
.gallery__header > p {
  font-weight: 400;
  line-height: 1.6;
}
.gallery__layout {
  margin-bottom: 24px;
  text-align: right;
}
.gallery__layout__btn {
  width: 20px;
  fill: #808080;
  margin: 0 10px;
  cursor: pointer;
}
.gallery__layout__btn:hover {
  fill: #111;
}
.gallery__layout__btn--active {
  fill: #111;
}


