
.list-item {
  margin-bottom: 100px;
}
.list-item__photo {
  width: 100%;
  height: 100%;
  background-size: cover;
  cursor: zoom-in;
}
.list-item__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 20px;
}
.list-item__info-likes {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
  color: #999;
  border: 1px solid #ddd;
  padding: 6px 15px;
  border-radius: 5px;
}
.list-item__info-likes > svg {
  color: #f15151;
  margin-right: 5px;
}
.list-item__info-user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.list-item__info-user > img {
  border-radius: 50%;
  margin-right: 10px;
}
.list-item__info-user > a {
  color: #999;
}
.list-item__info-user > a:hover {
  color: #111;
}
.list-item__info-download {
  color: #999;
  fill: currentColor;
  font-weight: bold;
  background-color: transparent;
  border: #ddd 1px solid;
  border-radius: 5px;
  padding: 6px 15px;
  text-decoration: none;
}
.list-item__info-download:hover {
  color: #111;
  border-color: #999;
  box-shadow: 0 1px 2px rgba(0,0,0,0.08);
  transition: all 0.2s ease-in-out;
}


