
.cover-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: rgba(0,0,0,0.5);
  cursor: zoom-out;
}
.cover-image > ._loading {
  position: absolute;
  left: 0;
  right: 0;
  top: 25vh;
}
.cover-image__image {
  height: 100%;
  background-position: 50% 50%;
  background-size: cover;
}


